import React from 'react'
import styles from './MobileMenu.module.scss'
import { Link } from 'react-router-dom'
import AppContext from '../../context'
import { useCart } from '../../hooks/useCart'

function MobileMenu() {
  const { totalPrice } = useCart()
  const {
    showCase,
    mobileMenuOpened,
    setCartOpened,
    setMobileMenuOpened
  } = React.useContext(AppContext)

  const menuValues = [
    { id: 1, name: '', value: '' },
    { id: 2, name: ` (${totalPrice} )`, menuItemFunc: setCartOpened },
    { id: 3, name: '', value: 'favorites' },
    { id: 4, name: ' ', value: 'orders' },
  ]

  return (
    <menu className={`${styles.mobileMenu} ${mobileMenuOpened ? styles.mobileMenuVisible : ''}`}>

      <h2 className={styles.titleCart}>
        
        <img
          onClick={() => {
            if (setMobileMenuOpened) {
              return setMobileMenuOpened(false)
            }
          }
          }
          className={styles.removeBtn}
          width="32"
          height="32"
          src="img/btn-remove.svg"
          alt="Close"
        />
      </h2>
      <ul className={styles.mobileMenuBox}>
        {menuValues.map((obj, index) => (
          (
            obj.menuItemFunc ?
              <li
                key={index}
                className={styles.mobileMenuItem}
                onClick={() => {
                  if (obj.menuItemFunc) { obj.menuItemFunc(true) }
                  if (setMobileMenuOpened) {
                    return setMobileMenuOpened(false)
                  }
                }}>
                {obj.name}
              </li>
              :
              <Link
                key={index}
                to={`${showCase}${obj.value}`}
                onClick={() => {
                  if (setMobileMenuOpened) {
                    return setMobileMenuOpened(false)
                  }
                }}>
                <li className={styles.mobileMenuItem}>{obj.name}</li>
              </Link>
          )
        ))}
      </ul>
    </menu>
  )
}

export default MobileMenu